import {createRouter, createWebHistory} from 'vue-router';
import HomeView from '../views/HomeView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/about',
    name: 'about',
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path : '/cart',
    name : 'cart',
    component: () => import('../views/client/DJ_C_Cart.vue')
  },
  {
    path : '/ordPlaced',
    name : 'ordPlaced',
    component: () => import('../views/client/DJ_C_Order.vue')
  },
  {
    path: '/main',
    name: 'main',
    component: () => import('../views/client/C_Main.vue')
  },
  {
    path:'/shoplist',
    name:'shoplist',
    component:() => import('../views/client/DJ_C_CateShop.vue')
  },
  {
    path: '/shopinfo',
    name: 'shopinfo',
    component: () => import('../views/client/DJ_C_ShopInfo.vue')
  },
  {
    path: '/item/itemView',
    name: 'itemView',
    component: () => import('../views/client/DJ_C_MenuInfo.vue')
  }, 
  {
    path:'/biz/status',
    name: 'bizOrderingStatus',
    component:() => import('../views/business/B_Order.vue')
  }
]
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router;
