// 최종 
const cartStore = {
    namespaced: true,
    // 변수 정의
    state: {
        cartList: [
            {
                fname: '짬뽕',
                fnum: '1',
                foption: '곱빼기(+500원)',
                fprice: 5000,
                ftotal:5000,
                fcnt: 1,
                osfnum:2000
            },
            {
                fname: '짬뽕',
                fnum: '2',
                foption: '곱빼기(+500원)',
                fprice: 5000,
                ftotal:5000,
                fcnt: 1,
                osfnum:2200
            }
        ]
    },
    getters: { // 상태 가져오는 함수
        GET_CART_LIST: state => state.cartList,
        TOTAL_CART:(state)=>{
            let sum = 0
            state.cartList.forEach(order => {
                sum += order.ftotal;
            });
            console.log(sum + "c_cartStore 합계")
            return sum;
        }
    },
    // 상태 변경
    mutations: {
        MU_CART_LIST: (state, payload) => { 
            state.cartStore = payload;
        },
        ADD_CART_LIST: (state, product) => {
            state.cartList.push(product);
            console.log(product + "  stroe produ");
        },
        INCREASE_CNT:(state,osfunm) => {
        
            // 해당 osfnum을 가진 상품
            const target_product = state.cartList.find(item => item.osfnum == osfunm);
            // console.log(target_product)
            // console.log(target_product.fcnt + " :Before");
            target_product.fcnt += 1;
            // console.log(target_product.fcnt + " :After");
            target_product.ftotal = target_product.fprice * target_product.fcnt;
            
        },
        DECREMENT_CNT :(state,osfunm)=>{
            // 해당 osfnum을 가진 상품
            const target_product = state.cartList.find(item => item.osfnum == osfunm);
            // console.log(target_product)
            // console.log(target_product.fcnt + " :Before");
            target_product.fcnt -= 1;
            // console.log(target_product.fcnt + " :After");
            target_product.ftotal = target_product.fprice * target_product.fcnt;
            
        },
        RM_CART_LIST:(state, osfnum) =>{
            const target_product = state.cartList.find(item => item.osfnum == osfnum);
            // state.cartList.splice(index, 1);
            console.log(state.cartList)
            // state.cartList.splice(osfnum, osfnum)
            
            state.cartList = state.cartList.filter(obj => !compareObjects(obj, target_product));
            
            function compareObjects(obj1, obj2) {
                return JSON.stringify(obj1) === JSON.stringify(obj2);
            }
            console.log(state.cartList)
        },
       
    } 
}
export default cartStore