<template>
  <div class="home">
    <input v-model="mnum" type="text" placeholder="Enter mnum">
    <button @click="saveMnum">Save</button>
    <a href="/about">lego</a>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import axios from 'axios'

export default {
  data() {
    return {
      mnum: ''
    }
  },
  methods: {
    saveMnum() {
      Cookies.set('mnum', this.mnum, { expires: 1 }) // 쿠키 설정
      console.log('Saved mnum to cookie:', this.mnum)
    }
  },
  mounted(){
    let value = Cookies.get('mnum') // 쿠키 읽기
    console.log(value)

    axios.get('/test').then(res => {
      console.log(res.data," data response")
    })
  }
}
</script>
